import { h } from 'preact';
import { Board }  from '../lib/vowelboard'
import { BoardPosition } from '../lib/wordboard';

interface LetterTileProps { 
    x: number, 
    y: number, 
    letter: string, 
    selected: boolean,
    onTrySelectLetter?: (row:number, col: number, letter: string) => void
}

export const LetterTile = ( { letter, selected, x, y, onTrySelectLetter }: LetterTileProps) => {
    var inner = selected ? <b> { letter } </b> :  letter 

    let raiseLetterSelect= (e: PointerEvent) => {
       

        if (onTrySelectLetter) {
            if (e.pressure > 0) {
                var overEl = document.elementFromPoint(e.pageX, e.pageY);
                if (overEl != e.currentTarget &&  !(e.currentTarget as Element).contains(overEl) ) {
                    overEl.dispatchEvent(new PointerEvent('pointermove',e))
                    return;
                }
                onTrySelectLetter( y, x, letter)
            }
        }  
    }

    return (   
        <span class={"letter-tile" + (selected ? " selected" : "") }  onPointerMove={raiseLetterSelect}>
        { inner }
        </span> 
    )
}

interface LetterGridProps {
    selectedLetters: BoardPosition[]
    board: Board
    onTrySelectLetter?: (row:number, col: number, letter: string) => void
}

export function LetterGrid({ board, onTrySelectLetter, selectedLetters }:LetterGridProps): JSX.Element {
    let letters = board.Letters.map( 
        (row, rownum) => row.map( 
            (letter, colnum) => (
                <LetterTile x={colnum} y={rownum} letter={letter} selected={selectedLetters.find(l => l.Row == rownum && l.Col == colnum) != null } onTrySelectLetter={onTrySelectLetter}></LetterTile> 
            ) 
        )
    )

    return (
      <div class="letter-grid">
        { letters }  
      </div>
    )
}
